import React from "react";
import PT from "prop-types";
import { LineChart, XAxis, YAxis, CartesianGrid, Tooltip, Line, Legend, ResponsiveContainer } from 'recharts';

const chartMargin = {top: 5, right: 30, left: 20, bottom: 5};

class RealEstateCalculator extends React.Component {

   static propTypes = {
      realEstates: PT.arrayOf(PT.shape({
         price: PT.number.isRequired,
         rent: PT.number.isRequired,
      })).isRequired,
      startPoint: PT.number,
      salary: PT.number.isRequired,
      expenses: PT.number,
      years: PT.number,
      showChart: PT.oneOf(["balance", "salary", "bought", "rent", "worth"]),
   }

   static defaultProps = {
      startPoint: 0,
      expenses: 0,
      years: 10,
      showChart: "balance",
   }

   computePredictions = () => {
      const sortRE = this.props.realEstates.sort((a, b) => a.price < b.price ? -1 : 1)
      const year = new Date().getFullYear()
      const n = this.props.years;
      const preds = [{ year, balance: this.props.startPoint }];
      let salary = this.props.salary;
      let salaryFromRenters = 0;
      let totalProperties = 0;
      let totalWorth = this.props.startPoint;

      for (let i = year + 1; i < year + n + 1; i++) {
         let balance = preds[preds.length - 1].balance;
         balance += salary * 12;
         balance -= this.props.expenses * 12;

         if (sortRE.length > 0 && sortRE[0].price < balance) {
            balance -= sortRE[0].price;
            salary += (sortRE[0].rent - sortRE[0].energies);
            salaryFromRenters += (sortRE[0].rent - sortRE[0].energies);
            totalProperties++;
            totalWorth += (sortRE[0].price - (salary*12))
            sortRE.shift();
         } else {
            totalWorth += salary * 12;
         }

         preds.push({
            year: i,
            balance,
            salary,
            rent: salaryFromRenters,
            bought: totalProperties,
            worth: totalWorth,
         });
      }

      return preds;
   }

   render() {
      const predictions = this.computePredictions();

      return (
         <ResponsiveContainer width={"100%"} height={400}>
            <LineChart data={predictions} margin={chartMargin}>
               <XAxis dataKey="year"/>
               <YAxis dataKey={this.props.showChart} domain={[0, 'auto']} />
               <CartesianGrid strokeDasharray="3 3"/>
               <Tooltip/>
               <Legend />
               <Line type="monotone" dataKey={this.props.showChart} stroke="#8884d8"/>
            </LineChart>
         </ResponsiveContainer>
      )
   }
};

export default RealEstateCalculator;