import React from "react";
import RealEstateComponent from "../../../components/calculators/RealEstateBusinessCalculator"

class RealEstatePage extends React.Component {
   constructor() {
      super();
      this.state = {
         showChart: "balance"
      }
   }

   render() {
      return (
         <div>
            <div>
               <select 
                  value={this.state.showChart} 
                  onChange={(e) => this.setState({ showChart: e.target.value })}
               >
                  <option value={"balance"}>Balance</option>
                  <option value={"salary"}>Salary</option>
                  <option value={"bought"}>Bought</option>
                  <option value={"rent"}>Rent</option>
                  <option value={"worth"}>Total Worth</option>
               </select>
            </div>
            <RealEstateComponent 
               realEstates={[
                  { price: 50000, rent: 300, energies: 100 },
                  { price: 50000, rent: 300, energies: 100 },
                  { price: 50000, rent: 300, energies: 100 },
                  { price: 100000, rent: 3000, energies: 120 },
               ]}
               startPoint={0}
               salary={2000}
               years={20}
               expenses={350}
               showChart={this.state.showChart}
            />
         </div>
      )
   }
}

export default RealEstatePage